<template>
  <div class="sidebar">
    <el-menu :default-active="onRoutes" class="el-menu-vertical-demo el-menu--dark"  unique-opened router background-color="#324157">
      <el-menu-item index="/dashboard">
          <i class="el-icon-fa-tachometer"></i> Dashboard</el-menu-item>
      <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-fa-cogs"></i> 项目配置</template>
        <el-menu-item index="/test">
          <i class="el-icon-fa-leaf"></i> 应用管理</el-menu-item>
        <el-menu-item index="/process">
          <i class="el-icon-fa-play-circle"></i> 流程管理</el-menu-item>
        <el-menu-item index="/node">
          <i class="el-icon-fa-tasks"></i> 节点管理</el-menu-item>
        <el-menu-item index="/expression">
          <i class="el-icon-fa-cubes"></i> 表达式管理</el-menu-item>
        <el-menu-item index="/db">
          <i class="el-icon-fa-database"></i> 数据源管理</el-menu-item>
        <el-menu-item index="/inf">
          <i class="el-icon-fa-internet-explorer"></i> http接口管理</el-menu-item>
      </el-submenu>
      <el-submenu index="3">
        <template slot="title">
          <i class="el-icon-fa-history"></i>日志报表</template>
        <el-menu-item index="/execution">
          <i class="el-icon-fa-flash"></i> 运行管理</el-menu-item>
      </el-submenu>
    </el-menu>
    <div class="logo">
      © 2017 SYL, Inc. YC. CQ
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      roles: [],
      permissions: []
    };
  },
  methods: {
    hasPermission(p) {
      for (var index in this.permissions) {
        if (this.permissions[index] == p) return true;
      }
      return false;
    }
  },
  computed: {
    onRoutes() {
      return this.$route.path;
    }
  },
  mounted() {
    this.roles = localStorage.getItem("roles")
      ? JSON.parse(localStorage.getItem("roles"))
      : [];
    this.permissions = localStorage.getItem("permissions")
      ? JSON.parse(localStorage.getItem("permissions"))
      : [];
  }
};
</script>

<style scoped>
.sidebar {
  display: block;
  position: absolute;
  width: 250px;
  left: 0;
  top: 70px;
  bottom: 0;
  background: #2e363f;
}

.sidebar > ul {
  height: 100%;
}

.logo {
  position: absolute;
  left: 10px;
  top: 90%;
  color:#bfcbd9;
  transform: translateY(-60%);
}
</style>



